
<template>
  <div class="msg-detail-container">
    <mt-header title="消息详情" class="boder-header">
      <mt-button icon="back" slot="left" @click="reback"></mt-button>
    </mt-header>

    <div class="msg-detail-content" :style="'height:'+contentHeight+'px'">
      <div class="title-style">{{msgItem.title}}</div>

      <div class="date-style">
        {{msgItem.addtime}}
      </div>
      <div class="content-style" v-html="msgItem.content"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      msgItem: [],
      contentHeight: 0
    };
  },
  created() {
    this.msgItem = this.$route.query.msgItem;
  },

  mounted() {
    var screenHeight = window.screen.availHeight || window.screen.height;
    this.contentHeight = screenHeight - 44;
    console.log(this.contentHeight);
  },
  components: {},

  computed: {},

  methods: {
    reback() {
      this.$router.back();
    }
  }
};
</script>
<style lang='postcss'>
.msg-detail-container {
  min-height: 668px;
  & .title-container {
    background: #ffffff;
    padding-top: 30px;
    height: 64px;
    display: flex;
    justify-content: space-between;
    & .left-img {
      height: 32px;
      width: 24px;
      margin-left: 7px;
    }
    & .title-sytle {
      font-size: 18px;
      color: #253b40;
      flex-grow: 1;
      text-align: center;
    }
    & .right-text {
      font-size: 14px;
      color: #092126;
      margin-right: 12px;
    }
  }

  & .msg-detail-content {
    overflow-y: auto;
    & .title-style {
      margin-top: 15px;
      font-size: 32px;
      color: #1c2042;
      text-align: center;
    }

    & .date-style {
      margin-top: 7px;
      font-size: 12px;
      color: #8d929c;
      text-align: center;
    }

    & .content-style {
      margin-top: 20px;
      margin-left: 20px;
      margin-right: 20px;
      & img {
        max-width: 100%;
      }
    }
  }
}
</style>


